body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  background-color: #f5f5f5;
}

.container {
  max-width: 600px;
  margin: 20px auto;
  padding: 20px;
  background-color: #ffffff;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  overflow-y: auto;
  max-height: 100vh;
}

label {
  display: block;
  margin-bottom: 8px;
  font-weight: bold;
  color: #333333;
}

input[type="text"],
input[type="number"],
select,
textarea {
  width: 100%;
  padding: 8px;
  margin-bottom: 10px;
  border: 1px solid #dddddd;
  border-radius: 4px;
}

input[type="file"] {
  margin-bottom: 10px;
}

.error {
  color: red;
  font-size: 0.9em;
}

button {
  padding: 10px 15px;
  background-color: #007bff;
  color: #ffffff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

button:hover {
  background-color: #0056b3;
}

.field-array > div {
  margin-bottom: 20px;
  padding: 15px;
  border: 1px solid #dddddd;
  border-radius: 4px;
}

.field-array button {
  margin-top: 10px;
}

.hidden {
  display: none;
}

.legend {
  font-weight: bold;
  color: #666666;
  margin-bottom: 10px;
}

table {
  width: 100%;
  border-collapse: collapse;
  margin-bottom: 10px;
}

table, th, td {
  border: 1px solid #dddddd;
}

th, td {
  padding: 8px;
  text-align: left;
}

@media (max-width: 768px) {
  .container {
    margin: 10px;
    padding: 15px;
  }

  button {
    width: 100%;
  }
}
.container {
  max-width: 600px;
  margin: 0 auto;
  padding: 20px;
  background-color: #fff8dc; /* Color similar al trigo */
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.talles-table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 20px;
}

.talles-table th, .talles-table td {
  border: 1px solid #ddd;
  padding: 8px;
}

.talles-table th {
  background-color: #f2e6d9; /* Color similar a la harina */
  color: #333;
}

.error {
  color: red;
  font-size: 12px;
}
